<template>
  <div class="register">
    <header>
      <div class="logobox">
        <div class="logo"></div>
        <div class="logontag">Orange</div>
      </div>
      <router-link class="logintxt" to="/home">
        <van-icon class="cancel"  name="cross" size="22px" color="#000" />
      </router-link>
    </header>

    <div class="inputBox">
      <div class="introduce">Welcome to Orange Movie!</div>

      <!-- 注册页面 -->
      <div class="registermodule">
            <van-cell-group :border="false">
              <van-field type="text" v-model="user.value"
              right-icon="question-o" placeholder="用户名" :error-message="user.errorMsg"
              @input="validUsername(user.value)"
              :border="false"/>

              <van-field  v-model="pwd.value" :type="pwd.type"
              placeholder="密码" :right-icon="pwd.icon" 
              :error-message="pwd.errorMsg"
              @click-right-icon="checkPwd('pwd')"
              @input="validPwd(pwd.value)"
               :border="false"/>

              <van-field v-model="repwd.value" :type="repwd.type"  
              placeholder="确认密码" :right-icon="repwd.icon" 
              :error-message="repwd.errorMsg" 
              @click-right-icon="checkPwd('repwd')"
              @input="validThesame(pwd.value,repwd.value)"
               :border="false"/>

              <!-- <van-field type="text" v-model="user.value"
              right-icon="question-o" placeholder="请输入手机号码" :error-message="user.errorMsg"
              @input="validUsername(user.value)"
              :border="false"/>

              <van-field center clearable placeholder="请输入短信验证码">
                <van-button slot="button" size="small">发送验证码</van-button>
              </van-field> -->

              <van-button class="registerBtn" :loading="isLoading" 
                  type="info" block loading-text="注册中..." 
                  text="注册"  @click="register" :disabled="!isValid"
              />
            </van-cell-group>

            <div class="login">
              <router-link class="logintxt" to="/login">已有账号？<span>登录</span> </router-link>
            </div>
      </div>
    </div>

    <footer>
      <span>注册代表您同意该软件
        <a href="">用户服务协议</a>和
        <a href="">隐私政策</a>
      </span>
    </footer>
  </div>
</template>

<script>
import {validForm} from '../tool/validForm'

import { createNamespacedHelpers } from "vuex";
//基于registerModule的命名空间导入mapState、mapMutations 辅助函数
const {mapState,mapMutations} = createNamespacedHelpers('registerModule');

export default {
  name: "register",
  
  // 原vue定义模型
  data(){
    return{
      active:1,
    };
  },

  // 计算属性
  computed:{
    // 数组的导入方式
    ...mapState(['pwd','repwd','isValid','isLoading','user'])
  },

  methods:{
    // 查看密码及修改状态
    ...mapMutations(['checkPwd']),

    // 注册验证  
    register(){
      let self = this;

      self.$store.commit('registerModule/register',true); //注册

      // 5S内随机时间
      let randomTime = Math.random() * 5 * 1000;
      
      //注册延时
      setTimeout(function(){

        // 4获取所有用户信息(一开没有注册始拿不到)
        let allusersInfo = localStorage.getItem('allusersInfo');
        allusersInfo = allusersInfo ? JSON.parse(allusersInfo) : []; // 判断有没有值否则会报错一个null
        // console.log('allusersInfo=>',allusersInfo)

        // 判断用户是否存在
        for(let i =0 ; i<allusersInfo.length; i++){
          if(allusersInfo[i].username == self.user.value){

            //配置参数Toast 如果存在就还是先延迟2s再截断
              self.$toast({
                duration: 2000,
                message: '该用户已存在'
              })
              self.$store.commit('registerModule/register', false);
            return;
          }
        }

        // 配置参数Toast 注册成功转动停止vant自带
        self.$toast({
          duration:2000, // 转2秒就停止
          message:'注册成功,请完善您的信息！'
        })
        // 跳转到第二注册页面

        self.$router.push({name:'phoneRegister'});
        self.$store.commit('registerModule/register',false);

        // 2获取时间搓
        let time = new Date().getTime();

        // 记录用户信息对象存储
        let userInfo = {
          username:self.user.value, //用户名
          pwd:self.pwd.value,  //密码  未来nodejs加密
          time   //注册时间
        }

        //将当前用户信息推进allusersInfo
        allusersInfo.push(userInfo); 

        // 将用户信息放本地存储（模拟数据库） 转成json格式（序列化）才能放进数组或对象
        localStorage.setItem('allusersInfo',JSON.stringify(allusersInfo));

      },randomTime)

    },

    // 用户名验证
    validUsername(v){
      let isValid = validForm.validUsername(v);
      this.$store.commit('registerModule/validUsername',isValid);
      this.$store.commit('registerModule/passFun');
    },

    // 密码验证
    validPwd(v){
      // console.log('value=>',v); //是否有值传入
      let isValid = validForm.validPwd(v);
      // 这上下两个validPwd是不同文件里的哦！命名可以一样而已
      this.$store.commit('registerModule/validPwd',isValid);
      this.$store.commit('registerModule/passFun');
    },

    //验证用户名或者密码！！！！！！ 找不出错误!!!validForm[o.fnName]说没有定义
    // !!!!!!!!!!!!!!! 下次没有定义请一定一定细细查看相关单词，我的validForm.validPwd 写成了vaildPwd

    // validCommon(value,fnName,o){
    //   console.log('value=>',v); //是否有值传入
    //   // o.fnName就是validForm.vaildPwd 和 validForm.validUsername
    //   // let isValid = validForm[o.fnName](v); 将fnName作为形参传递

    //   let isValid = validForm[fnName](value);
    //   console.log('isValid=>',isValid);

    //   o.isValid = isValid;
    //   this.$store.commit('registerModule/validCommon',o);
    // },
    
    //密码一致判断 
    validThesame(v1,v2){
      let isValid = validForm.validThesame(v1, v2);

      this.$store.commit('registerModule/validThesame', isValid);
      this.$store.commit('registerModule/passFun');
    },

  }

// 页面测试用于检查是否方法是否成功 
  // methods: {
  //   checkPwd(){
  //     console.log('查看密码！');
  //   }
  // }

};
</script>

